@import '../../utils/breakpoints.scss';

.header {
  display: flex;
  width: 100%;
  height: 65px;
  background-color: white;
  z-index: 10000;
  position: fixed;
  border-bottom: 1px solid #0c112e;

  &__container {
    display: flex;
    width: 100%;
    height: 100%;
    // gap: 50px;
    // justify-content: space-between;
    align-items: center;
    padding-right: 50px;
    padding-left: 50px;
    // background-color:blue;
    @include small-screnn {
      padding-right: 15px;
      padding-left: 15px;
    }
  }

  &__logo-name {
    color: #000000;
    font-size: 24px;
    font-family: 'Arial', Arial, sans-serif;
    line-height: 1.15;
    font-weight: 400;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
  }

  &__logo-block {
    display: flex;
    max-width: 250px;
    height: 90%;    
  }

  &__logo {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }

  &__nav-list {
    display: flex;
    flex-direction: row;
    gap: 20px;
    
  }

  &__nav-item {
    display: flex;
    color: #0c112e;
    font-size: 16px;
    font-family: 'Arial', Arial, sans-serif;
    line-height: 1.15;
    font-weight: 600;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
    cursor: pointer;
    &:hover {
      color: rgb(255, 192, 0);
      font-weight: bold;
    }
    &--active {
      color: rgb(255, 192, 0);
      font-weight: bold;
    }
    @include xlarge-screen {
      display: none;
    }
  }

  &__contact-button {
    width: 124px;
    height: 40px;
    color: #000000;
    font-size: 14px;
    font-family: 'Arial', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 700;
    border-color: #000000;
    border-radius: 30px;
    background-color: #ffffff;
    background-position: center center;
    border-style: solid;
    border-width: 1px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out;
    @include xlarge-screen {
      display: none;
    }
    &:hover{
      background-color: #0c112e;
      color: white;
    }
  }

  &__right-block {
    display: flex;
    justify-content: end;
    width: 100%;
    gap: 20px;
    align-items: center;
  }

  // &__burger-menu {
