.menu-content {
  display: none;
  flex-direction: column;
  position: absolute;
  align-items: center;
  top: 60px;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  transition: height 0.9s ease, opacity 0.5s ease;

 
  &.show {
    display: flex;
    transition: height 0.9s ease, opacity 0.5s ease;
    pointer-events: auto; /* Enable pointer events when the menu is visible */

  }
 
  &__container {
    display: flex;
    width: 100%;
    height: 100%;
    transition: height 0.9s ease, opacity 0.5s ease;
  }
 
  &__nav-list {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
    min-height: 600px;
    padding-top: 20px;
    background-color: #ffffff;
    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.2);
  }

  &__nav-item {
    color: #0c112e;
    font-size: 16px;
    font-family: 'Arial', Arial, sans-serif;
    line-height: 1.15;
    font-weight: 600;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #f2f2f2;
    &:hover {
      color: rgb(255, 192, 0);
      font-weight: bold;
    }
  }

  &__contacts-button {
    display: flex;
    align-self: center;
    width: 100%;
    max-width: 240px;
    height: 40px;
    color: #000000;
    font-size: 14px;
    font-family: 'Arial', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 700;
    border-color: #000000;
    border-radius: 30px;
    background-color: #ffffff;
    border-style: solid;
    border-width: 1px;
    margin-top: 50px;
    align-items: center;
    justify-content: center;
  }
}

.burger-button {
  display: none;
  background: none;
  width: 40px;
  height: 40px;
  border: none;
  cursor: pointer;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &__label {
    display:none;
    flex-direction:column;
    width: 100%;
    cursor:pointer;
    @include xlarge-screen {
      display: flex;
    }
  }

  &__burger-line {
    background: #0c112e;
    border-radius:10px;
    height:4px;
    margin: 4px 0;
    transition: .3s  cubic-bezier(0.68, -0.6, 0.32, 1.6);
    &:nth-of-type(1){
      width:50%;
    }
    &:nth-of-type(2){
      width:100%;
    }
    &:nth-of-type(3){
      width:75%;
    }
  }
}


 
