@import '../../utils/breakpoints.scss';

.footer {
  display: flex;
  width: 100%;
  height: 70px;
  background-color: #0c112e;
  &__container {
    display: flex;
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: center;
    justify-content: center;
  }

  &__logo {
    width: 20%;
    height: 100%;
    object-fit: contain;
    @include medium-screen {
      width: 40%;
    }
  }
}