@import '../../utils/breakpoints.scss';

.steps-section {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
  background-color: #fff;
  padding-top: 100px;
  padding-bottom: 50px;

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 50px;
    padding-right: 50px;
    @include small-screnn {
      padding-left: 50px;
      padding-right: 10px;
    }
  }

  &__main-title {
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 600;
    color: #000000;
    font-size: 38px;
    line-height: 1.25;
    margin-bottom: 80px;
    text-align: center;
    @include small-screnn {
      font-size: 28px;
      padding-right: 40px;
    }
  }

  &__steps-block {
    display: flex;
    width: 100%;
    flex-direction: column;
  }

  &__step-item--left {
    display: flex;
    flex-direction: column;
    align-items: start;
    width: 100%;
    min-height: 200px;
    border-left: 3px solid black;
    max-width: 900px;
   
    position: relative;
    &:last-child {
      border-left: none;
    }
    @include small-screnn {
      padding-bottom: 30px;
    }
  }

  &__step-txt {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-left: 50px;
    margin-right: 50px;
    @include small-screnn {
      margin-right: 10px;
    }
  }

 
  &__circle--left {
    position: absolute;
    border-radius: 50%;
    left: -40px;
    top: -5px;
    display: flex;
    align-items: center;
    justify-content: center;
    color :white;
    border: 5px solid white;
    width: 65px;
    height: 65px;
    background-color: black;
    font-size: 19px;
    line-height: 1.35;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 800;
  }

  &__step-title {
    font-size: 22px;
    line-height: 1.35;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 800;
    color: #000000;
  }

  &__step-desc {
    font-size: 18px;
    line-height: 1.55;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 300;
    color:  #4d4d4d;
  }
  
}

