@import '../../utils/breakpoints.scss';

.promotion-section {
  display: flex;
  width: 100%;
  min-height: 650px;
  align-items: center;
  justify-content: center;
  background-image: url('../../assets/prom-back.jpeg');
  background: linear-gradient(to bottom, rgba(12,17,46,0.90), rgba(12,17,46,0.90)), url('../../assets/prom-back.jpeg');
  &__container {
    display: flex;
    flex-direction: column;
    width: 1200px;
    height: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }

  &__main-title {
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 600;
    font-size: 48px;
    line-height: 1.23;
    color: #fff;
    margin-bottom: 25px;
    @include small-screnn {
      font-size: 30px;
    }
  }

  &__main-title--prom {
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 600;
    font-size: 48px;
    line-height: 1.23;
    color: rgb(255, 192, 0);;
    margin-bottom: 25px;
    @include small-screnn {
      font-size: 35px;
    }
  }

  &__desc--bold {
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.5;
    color: white;
    @include small-screnn {
      font-size: 20px;
    }
  }

  &__desc {
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 200;
    font-size: 20px;
    line-height: 2;
    color: whitesmoke;
    max-width: 600px;
    @include small-screnn {
      font-size: 20px;
    }
  }

  &__buy-btn {
    width: 225px;
    height: 60px;
    color: #000000;
    background-color: #ffc000;
    border-radius: 30px;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 700;
    cursor: pointer;
    font-size: 16px;
    margin-top: 70px;
    border: 0;
    &:hover {
      opacity: 0.9;
    }
  }
}