@import '../../utils/breakpoints.scss';

.speakers-section {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 900px;
  background-color: #0c112e;
  align-items: center;
  justify-content: center;
  padding-top: 30px;
  padding-bottom: 30px;
  @include medium-screen {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  &__container {
    display: flex;
    flex-direction: column;
    max-width: 1200px;
    height: 100%;
  }

  &__main-title {
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 600;
    font-size: 42px;
    line-height: 1.23;
    color: rgb(255, 255, 255);
    margin-bottom: 80px;
    text-align: center;
    padding-left: 15px;
    padding-right: 15px;
    @include small-screnn {
      font-size: 25px !important;
    }
    @include medium-screen {
      font-size: 30px;
    }
    

  }

  &__speaker-photo {
    width: 260px;
    min-height: 405px;
    object-fit: cover;
    display: inline-block;
    overflow: hidden; /* Скрыть часть картинки, которая выходит за пределы контейнера */
    transition: transform 0.3s ease-in-out; /* Плавное изменение масштаба при hover */
    &:hover {
      transform: scale(1.1);
      z-index: 600; /* Увеличение картинки до 1.2 раза при hover */
    }
  }

  &__org-block {
    position: relative;
  }

  &__org-line {
    width: 200px;
    height: 15px;
    position: absolute;
    background-color: orange;
    bottom: -10px;
    right: 0;
  }

  &__speakers-block {
    display: flex;
    gap: 50px;
    justify-content: center;
    flex-wrap: wrap;
  }

  &__speaker-card {
    display: flex;
    flex-direction: column;
    width: 260px;
    height: 100%;
  }

  &__speaker-name {
    font-size: 22px;
    line-height: 1.35;
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 600;
    color: white;
    margin-top: 30px;
  }

  &__speaker-position {
    font-size: 14px;
    line-height: 1.55;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 300;
    color:  whitesmoke;
    margin-bottom: 25px;
  }

  &__hr-line {
    width: 100%;
    height: 1px;
    background-color: gray;
    margin: 0;
  }
}