@import '../../utils/breakpoints.scss';

.reasons-section {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #f2f2f2;
  flex-direction: column;
  align-items: center;
  padding-top: 80px;
  padding-bottom: 80px;
  // justify-content: center;

  &__main-title {
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 600;
    color: #000000;
    font-size: 42px;
    line-height: 1.23;
    text-align: center;
    padding-left: 15px;
    padding-right: 15px;
    @include small-screnn {
      font-size: 30px;
    }
    @include medium-screen {
      max-width: 560px;
    }
  }

  &__smal-desc {
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 300;
    font-size: 24px;
    line-height: 1.5;
    color:  #4d4d4d;
    text-align: center;
    padding-left: 15px;
    padding-right: 15px;
  }

  &__table-content {
    display: flex;
    flex-wrap: wrap;
    max-width: 1000px;
    justify-content: center;
    gap: 30px;
    margin-top: 50px;
    padding-left: 15px;
    padding-right: 15px;
  }

  &__img {
    width: 45px;
    height: 45px;
  }

  &__reason-item {
    display: flex;
    max-width: 460px;
    height: 100%;
    gap: 20px;

  }

  &__txt-block {
    display: flex;
    flex-direction: column;
  }

  &__reason-title {
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 600;
    color: #000000;
    font-size: 22px;
    line-height: 1.35;
  }

  &__reason-text {
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 300;
    color:  #4d4d4d;
    font-size: 16px;
    line-height: 1.55;
  }
}