@import '../../utils/breakpoints.scss';

.price-section {
  display: flex;
  width: 100%;
  min-height: 800px;
  background-color: #0c112e;
  align-items: center;
  justify-content: center;
  @include small-screnn {
    min-height: 650px;
  }

  &__container {
    display: flex;
    flex-direction: column;
    max-width: 800px;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }

  &__main-title {
    color: #ffffff;
    font-size: 42px;
    font-family: 'Open Sans', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 700;
    @include small-screnn {
      font-size: 30px;
    }
  }

  &__description {
    color: #ffffff;
    font-size: 24px;
    font-family: 'Open Sans', Arial, sans-serif;
    line-height: 1.5;
    font-weight: 400;
    margin-bottom: 70px;
    @include small-screnn {
      font-size: 18px;
    }
  }

  &__main-box {
    display: flex;
    flex-wrap: wrap;
    max-width: 761px;
    width: 100%;
    min-height: 256px;
    position: relative;
    background-color: #eebd1d;
    border-color: transparent;
    background-position: center center;
    border-style: solid;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0);
  }

  &__ticket-box {
    display: flex;
    max-width: 761px;
    width: 100%;
    min-height: 256px;
    background-color: #fafafa;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0);
    position: absolute;
    right: 8px;
    bottom: 10px;
    flex-direction: column;
    // padding-left: 20px;
    /* padding: 20px; */
    @include small-screnn {
      padding-left: 0px;
    }
  }

  &__ticket-container {
    display: flex;
    flex-direction: column;
    padding: 20px;
    
  }

  &__ticket-title {
    font-size: 22px;
    font-family: 'Open Sans', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 700;
    margin-bottom: 30px;
    @include small-screnn {
      font-size: 20px;
      margin-bottom: 15px;
    }
   
  }

  &__dollar {
    color: rgb(238, 189, 29);
    font-size: 22px;
    font-family: 'Open Sans', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 700;
    margin-bottom: 20px;
    &--gray {
      color:  #4d4d4d;
    }
  }

  &__desc-box {
    display: flex;
    justify-content: space-between;
    @include small-screnn {
      flex-wrap: wrap;
    }
  }

  &__dest-txt {
    color: #4d4d4d;
    font-size: 18px;
    font-family: 'Open Sans',Arial,sans-serif;
    line-height: 1.55;
    font-weight: 400;
    opacity: 1;
    // width: 500px;
    @include small-screnn {
      font-size: 16px;
    }
  }

  &__reg-btn {
    color: #eebd1d;
    font-size: 14px;
    width: 100%;
    max-width: 170px;
    height: 45px;
    font-family: 'Open Sans',Arial,sans-serif;
    line-height: 1.55;
    font-weight: 600;
    border-width: 2px;
    border-radius: 30px;
    background-position: center center;
    border-color: #eebd1d;
    background-color: white;
    border-style: solid;
    margin-top: 40px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, border-color 0.2s ease-in-out;
    @include small-screnn {
      margin-top: 20px;
    }
    &:hover {
      background-color: #eebd1d;
      color: white;
    }
  }
}