@import '../../utils/breakpoints.scss';

.questions-section {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 50px;
  padding-bottom: 100px;
  align-items: center;
  background-color: #eeeeee;

  &__container {
    padding-left: 20px;
    padding-right: 20px;
  }

  &__title-box {
    position: relative;
    border-bottom: 1px solid black;
    border-top: 1px solid black;
    padding-top: 40px;
    padding-bottom: 30px;
    display: flex;
    /* Add this to make the title-box a flex container */
    justify-content: center;
    /* Align the items (dark-title) horizontally in the center */

  }

  &__dark-box {
    position: absolute;
    width: 279px;
    height: 40px;
    background-color: black;
    top: -20px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__dark-title {
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    color: white;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 2px;
    @include small-screnn {
      font-size: 12px;
    }
  }

  &__main-title {
    font-size: 38px;
    line-height: 1.35;
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 300;
    color: #000000;
    margin-left: 20px;
    margin-right: 20px;
    text-align: center;
    @include small-screnn {
      font-size: 30px;
    }
  }

  &__faq-block {
    display: flex;
    width: 100%;
    margin-top: 30px;
    justify-content: center;
    flex-direction: column;
  }
}

#panel-header {
  padding: 20px;
  font-size: 24px;
  line-height: 1.35;
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: 600;
  color: #000000;
  @include small-screnn {
    font-size: 18px;
  }
}



#panel-content {
  padding-left: 10px;
  padding-right: 10px;

  padding-bottom: 10px;
  font-size: 18px;
  line-height: 1.55;
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: 500;
  color: #000000;
  height: 100%;
  @include small-screnn {
    font-size: 16px;
  }
}