@import '../../utils/breakpoints.scss';

.hero-section {
  position: relative;
  display: flex;
  width: 100%;
  min-height: 800px;
  // align-items: center;
  justify-content: center;
  background-color: #0c112e;
  color: white;
  // gap: 40px;
  // flex-wrap: wrap;
  padding-top: 50px;

  @include xlarge-screen {
    // padding-top: 100px;
  }
  @include small-screnn {
    // padding-top: 20px;
  }
  // @include medium-screen {
 
  // }
  &__left-block {
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    padding-right: 15px;
    // background-color: blue;
    justify-content: center;
    // width: 100%;
    max-width: 1170px;
    width: 100%;
    @include xlarge-screen {
      max-width: 880px;
      width: 100%;
      align-items: center;
      // margin: 0 auto;
      // background-color: #ffc000;
    }
    @include medium-screen {
      max-width: 560px;
    }
    @include small-screnn {
      align-items: start;

    }
  }

  &__desc-block {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__date {
    color: #ffffff;
    font-size: 24px;
    font-family: "Open Sans", Arial, sans-serif;
    line-height: 1.55;
    font-weight: 400;
  }

  &__main-title {
    color: #ffffff;
    font-size: 82px;
    font-family: 'Open Sans', Arial, sans-serif;
    line-height: 1.05;
    font-weight: 600;
    max-width: 500px;
    @include medium-screen {
      font-size: 70px;
    }
    @include small-screnn {
      font-size: 40px;
    }
  }

  &__main-title span {
    color: rgb(255, 192, 0);
  }

  &__conf-desc {
    color: #ffffff;
    font-size: 18px;
    font-family: 'Open Sans', Arial, sans-serif;
    line-height: 1.25;
    font-weight: 300;
  }

  &__btn-stack {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 40px;
  }

  &__reg-btn {
    color: #000000;
    width: 230px;
    height: 65px;
    font-size: 18px;
    font-family: 'Roboto', Arial, sans-serif;
    line-height: 1.4;
    font-weight: 550;
    letter-spacing: 1px;

    cursor: pointer;
    
    border-radius: 100px;
    background-color: #ffc000;
    border: 0;
    &:hover {
      opacity: 0.9;
    }
    @include small-screnn {
      width: 200px;
      height: 45px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
     
      
    }
  }

  &__about-btn {
    color: #000000;
    width: 230px;
    height: 65px;
    font-size: 19px;
    font-family: 'Roboto', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 550;
    border-radius: 30px;
    background-color: #ffffff;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
    cursor: pointer;
    &:hover {
      opacity: 0.8;
    }
    @include small-screnn {
      width: 200px;
      height: 45px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 14px;
    }
  }

  &__loc-block {
    display: flex;
    gap: 15px;
    align-items: center;
    margin-top: 25px;
  }

  &__loc-img {
    width: 15px;
    height: 22px;
    
  }

  &__loc-txt {
    color: #ffffff;
    font-size: 16px;
    font-family: 'Open Sans',Arial,sans-serif;
    line-height: 1.55;
    font-weight: 400;
    text-transform: uppercase;
  }

  &__right-block {
    display: flex;
    position: absolute;
    right: 0;
    // top: 100px;
    align-self: center;
    justify-content: center;
    max-width: 730px;
 
    // padding-left: 15px;
    // padding-right: 15px;
    // padding-bottom: 50px;


    @include xlarge-screen {
      display: none;
    }
    
   }

   &__img {
    width: 100%;
    height: 100%;
    min-height: 500px;
    object-fit: cover;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    opacity: 1; /* Fully opaque */
    animation: fadeFromRight 3s forwards; 
    // border-radius: 30px;
   /* Define the transition property with the desired duration */
    
  }
}

@keyframes fadeFromRight {
  0% {
    right: 0;
    opacity: 0;
  }
  100% {
    right: 100%; /* Move to the left */
    opacity: 1; /* Fade out */
  }
}