@import '../../utils/breakpoints.scss';

.schedule-section {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  align-items: center;
  background-color: #fff;
  padding-top: 80px;
  padding-bottom: 80px;
 
  &__main-title {
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 600;
    color: #000000;
    font-size: 38px;
    margin-bottom: 20px;
    @include small-screnn {
      font-size: 30px;
    }
  }

  &__date-range {
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 300;
    color:  #4d4d4d;
    font-size: 22px;
    margin-bottom: 100px;
  }

  &__schedule-table {
    display: flex;
    flex-direction: column;
    max-width: 1060px;
    // width: 100%;
    height: 100%;
    gap: 100px;
    margin-bottom: 50px;
    padding-right: 20px;
    padding-left: 20px;
   
    @include small-screnn {
      max-width: 340px;
      padding-right: 10px;
      gap: 70px
      ;
      padding-left: 10px;
    }
  }

  &__schedule-item {
    display: flex;
    width: 100%;
    height: 100%;
    gap: 20px;
    flex-wrap: wrap;
    
    @include xlarge-screen {
      max-width: 600px;
    }
  }

  &__schedule-time {
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 600;
    color: #000000;
    line-height: 1.35;
    font-size: 22px;
    margin-right: 100px;
    width: 280px;
    @include small-screnn {
      margin-right: 0px;
    }

  }

  &__schedule-right {
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 600px;
  }

  &__schedule-morning {
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 700;
    color: #000000;
    font-size: 18px;
    line-height: 1.55;
    font-style: italic;
    max-width: 200px;

    @include small-screnn {
      font-size: 16px;
    }
  }

  &__schedule-position {
    font-size: 18px;
    line-height: 1.55;
    font-family: 'Roboto',Arial,sans-serif;
    font-weight: 300;
    color: #000000;
    @include small-screnn {
      font-size: 16px;
    }
    &--bold {
      font-style: italic;
      font-size: 18px;
      line-height: 1.55;
      font-family: 'Roboto',Arial,sans-serif;
      font-weight: 700;
      color: #000000;
      @include small-screnn {
        font-size: 16px;
      }
    }
  }

  &__download-btn {
    color: #000000;
    border: 3px solid #000000;
    border-radius: 30px;
    font-family: 'Roboto',Arial,sans-serif;
    text-align: center;
    font-size: 16px;

    text-align: center;
    font-weight: 700;
    background: none;
    cursor: pointer;
    height: 60px;
    width: 400px;
    @include small-screnn {
      width: 80%;
    }
    &:hover {
      transition: 0.3s;
      transform: scale(1.1);
    }
  }
}