@import '../../utils/breakpoints.scss';

.video-section {
  display: flex;
  width: 100%;
  min-height: 800px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #fff;
  &__content {
    display: flex;
    width: 90%;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    @include small-screnn {
      padding-top: 50px;
      padding-bottom: 50px;
    }
    @include medium-screen {
      padding-top: 50px;
      padding-bottom: 50px;
    }
  }
  

  &__iframe-block {
    display: flex;
    width: 100%;
    max-width: 560px;
    min-height: 450px;
    @include small-screnn {
      min-height: 350px;
    }
    @include medium-screen {
      max-width: 700px;
    }
  }

  &__iframe-vid {
    width: 100%;
    height: 100%;
  }

  &__content {
    display: flex;
    gap:50px;
    max-width: 1500px;
    flex-direction: column;
  }

  &__top-block {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 50px;
    @include small-screnn {
      gap: 20px;
    }
  }

  &__description {
    display: flex;
    flex-direction: column;
    align-items: start;
    width: 100%;
    max-width: 550px;
    @include medium-screen {
      max-width: 700px;
    }
  
   
    
  }

  &__title {
    color: #000000;
    font-size: 42px;
    font-family: 'Arial', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 600;
    @include small-screnn {
      font-size: 30px;
    }
  }

  &__smal-desc {
    color:  #4d4d4d;
    font-size: 16px;
    font-family: 'Arial', Arial, sans-serif;
    line-height: 1.7;
    font-weight: 400;
    margin-bottom: 15px;
    max-width: 380px;
    @include medium-screen {
      max-width: 700px;
    }
   
    
  }

  &__hr-desc {
    width: 50px;
    height: 5px;
    background-color: #ffc000;
    border: 0;
    display: flex;
    margin: 0;
    padding: 0;
    margin-bottom: 15px;
  }

  &__big-desc {
    color: #000000;
    font-size: 20px;
    font-family: 'Arial', Arial, sans-serif;
    line-height: 1.55;
    font-weight: 400;
    max-width: 560px;
    @include medium-screen {
      max-width: 700px;
    }
  }

  &__footer {
    display: flex;
    max-width: 1200px;
    width: 100%;
    min-height: 100px;
    align-self: center;
    border-top: 2px solid gray;
    border-bottom: 2px solid gray;
    padding-top: 30px;
    padding-bottom: 30px;
    justify-content: center;
    @include medium-screen {
      // max-width: 700px;
      // align-self: center;
      display: none;
    }
  }

  &__footer-container {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }

  &__table-content {
    display: flex;
  }

  &__img {
    width: 45px;
    height: 45px;
  }

  &__reason-item {
    display: flex;
    width: 460px;
    height: 265px;
    gap: 20px;
    background-color: antiquewhite;
  }

  &__animated-txt {
    font-size: 30px;
    line-height: 1.55;
    font-family: 'Roboto',sans-serif;
    font-weight: 300;
    color: #08cdab;
    text-align: center;
    &--normal {
      color: #000000;
    }
    @include small-screnn {
      font-size: 24px;
    }
  }
}