@import '../../utils/breakpoints.scss';

.location-section {
  display: flex;
  width: 100%;
  min-height: 1400px;
  background-color: white;
  justify-content: center;

  &__container {
    display: flex;
    max-width: 1200px;
    width: 100%;
    
    padding-bottom: 20px;
    // padding-left: 20px;
    // padding-right: 20px;
    height: 100%;
    flex-direction: column;
    margin-bottom: 100px;
    flex-wrap: wrap;
    @include small-screnn {
      padding-right: 0px;
      padding-left: 0px;
    }
  }

  &__map-block {
    display: flex;
    // flex-wrap: wrap;
    width: 100%;
    
    min-height: 660px;
    background-color: #f2f2f2;
    @include medium-screen {
      flex-wrap: wrap;
    }
  }

  &__desc-block {
    display: flex;
    // max-width: 560px;
    min-height: 660px;
    width: 50%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    @include small-screnn {
      min-height: 360px !important;
      padding-top: 15px;
      padding-bottom: 15px;
    }
    @include medium-screen {
      min-height: 450px;
      width: 100%;
    }


  }

  &__main-title {
    font-size: 42px;
    line-height: 1.23;
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 600;
    color: #000000;
    margin-top: 140px;
    margin-bottom: 45px;
    padding-left: 15px;
    @include small-screnn {
      font-size: 30px;
      margin-top: 50px;
      margin-bottom: 30px;
    }
  }

  &__date {
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 300;
    color: #000000;
    font-size: 24px;
    line-height: 1.5;
    max-width: 560px;
    margin-bottom: 45px;
    padding-left: 15px;
    @include small-screnn {
      font-size: 20px;
    }
  }

  &__street {
    font-size: 28px;
    line-height: 1.17;
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 600;
    margin-bottom: 45px;
    padding-left: 60px;
    color: #0c112e;
    @include small-screnn {
      padding-left: 15px;
      font-size: 22px;
    }
  }

  &__loc-desc {
    color: #0c112e;
    font-size: 18px;
    line-height: 1.55;
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 300;
    margin-bottom: 30px;
    padding-left: 60px;
    @include small-screnn {
      padding-left: 15px;
      font-size: 16px;
    }
  }

  &__contacts {
    color: #0c112e;
    font-size: 18px;
    line-height: 1.55;
    font-family: 'Roboto', Arial, sans-serif;
    font-weight: 300;
    padding-left: 60px;
    margin-bottom: 45px;
    @include small-screnn {
      padding-left: 15px;
      font-size: 16px;
    }
  }

  &__sn-stack {
    display: flex;
    padding-left: 60px;
    max-width: 120px;
    justify-content: space-between;
    @include small-screnn {
      padding-left: 15px;
    }
  }

  &__yandex-map {
    background-color: #0c112e;
    width: 50%;
    height: 100%;
    min-height: 660px;
    @include medium-screen {
      min-height: 450px !important;
      width: 100%;
    }
    @include small-screnn {
      min-height: 360px !important;
    }
  }
}

.location-section__yandex-map .ymaps-2-1-79-map {
  // width: 100%;
  // height: 100%;
  min-height: 660px;
  // @include small-screnn {
  //   height: 300px;
  // }
  @include medium-screen {
    min-height: 450px;
    max-height: 450px;
  }
  @include small-screnn {
    min-height: 360px !important;
    max-height: 360px;
  }
}

.photo-box {
  display: flex;
  position: relative;
  width: 100%;
  min-height: 388px;
  margin-top: 100px;
  @include medium-screen {

  }
  
  &__orange {
    display: flex;
    min-height: 388px;
    width: 100%;
    background-color: #eebd1d;
    margin-left: 30px;
    @include medium-screen {
      width: 60%;
      min-height: 800px;
      margin: 0 auto;
    }
    @include small-screnn {
      width: 85%;
    }
  }

  &__gallery {
    display: flex;
    width: 97%;
    position: absolute;
    z-index: 1000;
    min-height: 388px;
    flex-wrap: wrap;
    justify-content: space-between;
    right: 20px;
    bottom: 20px;
    
    max-width: 1170px;
    @include medium-screen {
      display: none;
      
    }
    &--medium {
      display: none;
      flex-direction: column;
      width: 100%;
      height: 100%;
      @include medium-screen {
        display: flex;
        
      }
    
    }
   
    
  }

  &__img {
    display: flex;
    width: 33.33%;
    height: 388px; /* Set the height for the images */
    object-fit: cover; /* Ensure the images fill the available space */
    @include medium-screen {
      width: 95%;
      height: 80%;
      margin-bottom: 20px;
    }

    
  }
}